h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

html {
  scroll-behavior: smooth;
}

.link {
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  // height: auto;
}

body {
  font-family: var(--font-Merriweather-Bold);
}

a {
  text-decoration: none;
  color: inherit;
}
